.h1{
  margin: 0px;
  padding: 0px;
  color:  $casava-black;
  font-family: GooperSemiCondensedLight;
  font-size: 32px;
  font-style: normal;
  font-weight: bold;
  line-height: 38.4px;
  letter-spacing: -0.32px;
  @media only screen and (max-width: 300px) {
    font-size: 26px;
    
  }

}
.amount{
  @extend .h1;
  font-family: GooperSemiCondensedSemibold;
}
.hmain{
  @extend .h1;
  font-size: 48px;
  line-height: 54px;
  letter-spacing: -2px;
  font-weight: 400;
  font-family: GooperSemiCondensed;


 
  @media only screen and (max-width: 300px) {
    font-size: 36px;
    
  }

}
.h2{
  margin: 0px;
  padding: 0px;
  color:  $casava-blackish-gray;
  font-family: Fellix;
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: 25.2px;

  @media only screen and (max-width: 300px) {
    font-size: 16px;
    
  }

}
.h2Medium{
  @extend .h2;
  font-family: FellixMedium;

}
.h3{
  @extend .h2;

  font-size: 16px;
  font-weight: 400;
  line-height: 22.4px;

  @media only screen and (max-width: 300px) {
    font-size: 14px;
    
  }

}


:root{
