  }
  .casava-section-hide {
    display: none;
  }

  .casava-section-show {
    display: unset;
  }

  .CasavaCheckout-overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 99999999;
    font-family:  "Fellix", "Fellix", "Inter", sans-serif;
    font-size: 11px;
  
    background: rgba(16, 30, 57, 0.5);
  }

  .CasavaCheckout-body {
    width: 100vw;
    height: 89vh;
    // height: 87vh;
    // height: 95vh;
    // height: 100vh;
    overflow-y: hidden;
    position: relative;
    background: #ffffff;
    box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.16);
    font-family: "Fellix", "Inter", sans-serif;
    // border-radius: 12px;
  }
  .casava-button{
    border-radius: 32px;
    border: none;
    background: $casava-inactive-pink;
    display: flex;
    width: 350px;
    padding:12px 24px;
    justify-content: center;
    align-items: center;

    color: $casava-white;
    font-family: Fellix;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: 20px;
    letter-spacing: -0.2px;
    text-transform: capitalize;
    cursor: pointer;

    &:hover {
      background: $casava-pink-dark
  }
  

    @media only screen and (max-width: 360px) {
    width: 90%;
    }


  }


  
  .casava-button-active{
    @extend .casava-button;
    background: $casava-pink;
  
  }

  .Casava-header-wrapper {

   
    position: relative;
    border-bottom: 1px solid #EAEAEA;;
    position: relative;
    height: 70px;
    margin-left:  1.25rem;
    margin-right:  1.25rem;
   
  }
  .Casava-header-wrapper::after {
    content: "";
    position: absolute;
    height: 2px;
    background-color: #fb0e7d;
    bottom: 0;
    width: var(--pseudo-header-width);
    left: 0;
    transition: all 0.3s ease-in-out;
  }

  .welcome-header{
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }

  .choose-plan-header{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .welcome-page-body{
    padding-left: 1.25rem;
    padding-right: 1.25rem;
    height: 70vh;
    // height: 70vh;
    overflow-y: scroll;
    overflow-x: hidden;
    padding-top: 24px;
    padding-bottom: 10px;
  }
  .welcome-page-body::-webkit-scrollbar {
    width: 6px;
    height: 6px;
  }

  .welcome-page-body::-webkit-scrollbar-thumb {
    border-radius: 4px;
    background-color: #D9D9D9;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    -o-border-radius: 4px;
}
