/* The container */
.select-hospital-page-hospitalist-item-container {
  display: block;
  position: relative;
  padding-left: 30px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.select-hospital-page-hospitalist-item-container .select-hospital-page-hospitalist-item-radio {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 14px;
  width: 14px;
  border: solid 2px #808080;
  border-radius: 50%;
}



.select-hospital-page-hospitalist-item-container .select-hospital-page-hospitalist-item-radio:checked ~ .checkmark {
  background-color: #FF147D;
  border: solid 2px #FF147D;

}

.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the indicator (dot/circle) when checked */
.select-hospital-page-hospitalist-item-container .select-hospital-page-hospitalist-item-radio:checked ~ .checkmark:after {
  display: block;
}

/* Style the indicator (dot/circle) */
.select-hospital-page-hospitalist-item-container .checkmark:after {
 

  top: 3.5px; left: 3.5px; width: 7px; height: 7px; border-radius: 50%; background: #fff;
}

