.channels-loader{
  border: 2px solid #FFE8F2;
  border-top: 2px solid #FF147D;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  animation: spin .5s linear infinite;
  -webkit-animation: spin .5s linear infinite;
}
  @keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
